.movie-modal {
  position: relative;
  padding: 48px 20px 40px;
  width: 100%;
  min-height: 800px;
  display: flex;
  flex-direction: column;

  background-color: rgba(255, 255, 255, 0.83);
  @media screen and (min-width: 480px) {
    position: absolute;
    top: 50%;
    left: 50%;
    padding: 40px 36px;
    transform: translate(-50%, -50%) scale(1);
    transform-origin: center;
    width: 400px;
    min-height: 580px;

    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }

  @include tablet {
    width: 704px;
    min-height: 470px;
    padding: 40px 72px 40px 36px;
  }

  @include desktop {
    width: 806px;
    min-height: 560px;
    padding: 40px 12px;
  }
}

.wrap-disc {
  max-width: 240px;
  margin-left: auto;
  margin-right: auto;

  @include tablet {
    max-width: 100%;
    display: flex;
    align-items: flex-start;
  }
}

.modal-poster {
  display: block;
  max-width: 100%;
  margin-bottom: 20px;
  border-radius: 5px;

  @include tablet {
    width: 264px;
    margin-right: 68px;
    margin-bottom: 0;
  }
  @include desktop {
    width: 375px;
    margin-right: 16px;
  }
}

.modal-movie-title {
  font-weight: 500;
  font-size: 20px;
  line-height: 23px;
  text-transform: uppercase;
  margin-bottom: 20px;

  @include desktop {
    font-size: 30px;
    line-height: 35px;
  }
}

.wrap-flex {
  display: flex;
  align-items: baseline;
}

.wrap-flex + .wrap-flex {
  margin-top: 8px;
}

.movie-label {
  font-size: 12px;
  line-height: 16px;
  color: #8c8c8c;

  min-width: 70px;
  margin-right: 40px;

  @include desktop {
    margin-right: 70px;
  }
}

.movie-disc {
  font-size: 12px;
  line-height: 14px;
}

.accent-detail {
  display: flex;
  justify-content: center;
  align-items: center;

  color: #ffffff;
  border-radius: 5px;
  width: 36px;
  height: 16px;
  background-color: #ff6b01;
}

.disc-wrap {
  display: flex;
  align-items: center;
}

.disc-space {
  margin: 0 3px;
  line-height: 16px;
  color: #8c8c8c;
}

.movie-disc-title {
  font-size: 12px;
  line-height: 16px;
  text-transform: uppercase;
}

.movie-disc-genres {
  font-size: 12px;
  line-height: 16px;
}

.disc-title {
  margin-top: 20px;

  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  text-transform: uppercase;
}

.disc-text {
  margin: 8px 0 20px;
  font-size: 12px;
  line-height: 20px;
}

.button-container {
  display: flex;
  justify-content: space-around;

  @media screen and (min-width: 480px) {
    justify-content: center;
  }

  @include desktop {
    justify-content: flex-start;
    padding-left: 8px;
  }
}
.modal__button-play {
  display: flex;
  border-color: #ff6b01;
  box-shadow: 0 0 25px 3px rgb(242 122 9 / 80%);
  background: transparent;
  margin: 20px 20px 0;
  justify-content: center;
}

.modal__img-play {
  width: 200px;
  height: auto;
}

.modal__button-play:hover {
  border: 2px #ff6b01;
  box-shadow: 0 0 25px 3px rgb(242 122 9 / 80%);

  background: radial-gradient(rgba(242, 122, 9, 0.8) 100%, #fff 62%);
}
.modal__button-play:active {
  box-shadow: 2px 2px 5px #fc894d;
}

.trailer__iframe {
  width: 90%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: absolute;

  @media screen and (min-width: 480px) {
    width: 700px;
    height: 470px;
  }
  @media screen and (min-width: 1200px) {
    width: 815px;
    height: 550px;
  }
}

.trailer__backdrop {
  z-index: 10;
}

.modal-btn {
  display: inline-block;
  padding: 0 10px;
  margin: 0;

  border: 1px solid #000000;
  border-radius: 5px;
  width: 110px;
  height: 44px;
  background-color: transparent;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  text-transform: uppercase;
  transition-property: background-color, color, border;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;

  @include tablet {
    width: 125px;
  }

  @include desktop {
    width: 136px;
  }

  &:hover,
  &:focus,
  &:active {
    background-color: #ff6b01;
    color: #ffffff;
    border: transparent;
  }
}

.btn-queue {
  @media screen and (min-width: 480px) {
    margin-left: 15px;
  }
}

.active-btn {
  background-color: #ff6b01;
  color: #ffffff;
  border: transparent;
}
